.header{
    background-color: $greyDark;
    border-radius: 0px 0px 10px 10px;
    box-shadow: 0px 5px 10px 2px rgba(32, 32, 32, 0.8);
    position: relative;
    z-index: 2;
    .header_nav{
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include tablet{
        justify-content: center;
    }
    
    }

    .logo{
        display: flex;
        align-items: flex-end;
        h1{
            margin-bottom: .5rem;
            font-size: 3.8rem;
            font-family: 'Comfortaa', cursive;
            color: white;
        }

        p{
            color: $greenPrimary;
            font-size: 6.5rem;
            margin: 0;
            font-weight: 600px;
            font-family: 'Comfortaa', cursive;
            line-height: 8rem;
        }

        img{
            width: 20rem;
            margin: auto -5rem -3rem 0 ;
        }
        
        @include tablet{
            h1{
                margin-bottom: .5rem;
                font-size: 2.8rem;
                font-family: 'Comfortaa', cursive;
                color: white;
            }
    
            p{
                color: $greenPrimary;
                font-size: 4.5rem;
                margin: 0;
                font-weight: 600px;
                font-family: 'Comfortaa', cursive;
                line-height: 6rem;
            }
    
            img{
                width: 16rem;
                margin: auto -3.8rem -2.5rem -4rem ;
            }
        }
        
    }
}