html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

.contenedor {
  max-width: 85%;
  margin: auto;
}

.section {
  height: 100vh;
  box-shadow: inset 0px 0px 25px 10px #1b1b1b;
}
.section:nth-of-type(2) {
  background-image: url("../img/bg_home.jpg");
  box-shadow: none;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

p {
  font-family: "Roboto", sans-serif;
  color: azure;
}

.glass {
  background-color: #363636;
  border-radius: 5px;
  padding: 0.5rem;
  animation: vibrate-1 4s linear infinite both;
  box-shadow: 0 0 50px black;
}
.glass:hover {
  animation: none;
}

.html5 {
  color: #e34c26;
}

.css3 {
  color: #264de4;
}

.javascript {
  color: #f0db4f;
}

.react {
  color: #61dbfb;
}

@keyframes vibrate-1 {
  0% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
  20% {
    -webkit-transform: translate(-2px, 2px);
    transform: translate(-2px, 2px);
  }
  40% {
    -webkit-transform: translate(-2px, -2px);
    transform: translate(-2px, -2px);
  }
  60% {
    -webkit-transform: translate(2px, 2px);
    transform: translate(2px, 2px);
  }
  80% {
    -webkit-transform: translate(2px, -2px);
    transform: translate(2px, -2px);
  }
  100% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}
body {
  background-color: #333333;
}

.important {
  cursor: pointer;
  font-weight: bolder;
  text-shadow: 1px 1px 4px #00211A;
  background-color: azure;
  user-select: none;
  display: inline-block;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
}
.important.active {
  animation: rotate 1s ease-out;
}
@keyframes rotate {
  20%, 80% {
    transform: rotateY(180deg);
  }
  50%, 100% {
    transform: rotateY(360deg);
  }
}

.button {
  display: block;
  background-color: #00735c;
  padding: 0.5rem;
  user-select: none;
  color: azure;
  border-radius: 5px;
  width: 15rem;
  text-align: center;
  font-size: 2rem;
  text-decoration: none;
  font-weight: 700;
}
.button:hover {
  background-color: #006450;
}

.header {
  background-color: #333333;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 0px 5px 10px 2px rgba(32, 32, 32, 0.8);
  position: relative;
  z-index: 2;
}
.header .header_nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1000px) {
  .header .header_nav {
    justify-content: center;
  }
}
.header .logo {
  display: flex;
  align-items: flex-end;
}
.header .logo h1 {
  margin-bottom: 0.5rem;
  font-size: 3.8rem;
  font-family: "Comfortaa", cursive;
  color: white;
}
.header .logo p {
  color: #00735c;
  font-size: 6.5rem;
  margin: 0;
  font-weight: 600px;
  font-family: "Comfortaa", cursive;
  line-height: 8rem;
}
.header .logo img {
  width: 20rem;
  margin: auto -5rem -3rem 0;
}
@media (max-width: 1000px) {
  .header .logo h1 {
    margin-bottom: 0.5rem;
    font-size: 2.8rem;
    font-family: "Comfortaa", cursive;
    color: white;
  }
  .header .logo p {
    color: #00735c;
    font-size: 4.5rem;
    margin: 0;
    font-weight: 600px;
    font-family: "Comfortaa", cursive;
    line-height: 6rem;
  }
  .header .logo img {
    width: 16rem;
    margin: auto -3.8rem -2.5rem -4rem;
  }
}

.nav a {
  text-decoration: none;
  color: white;
  padding: 1.5rem 1rem;
  font-family: "Montserrat", sans-serif;
  transition: 0.1s;
  font-weight: bold;
}
.nav a:hover {
  color: #00735c;
}
.nav a:last-of-type {
  background-color: #00735c;
  padding: 1.5rem 2.5rem;
}
.nav a:last-of-type:hover {
  color: #333333;
}
.nav i {
  display: none;
  visibility: hidden;
}
@media (max-width: 1000px) {
  .nav {
    background-color: #333333;
    box-shadow: 0px -2px 10px 0px #1b1b1b;
    position: fixed;
    width: 100%;
    z-index: 3;
    bottom: 0;
    height: 6.5rem;
    display: flex;
    justify-content: space-around;
  }
  .nav a {
    display: none;
    visibility: hidden;
  }
  .nav i {
    display: flex;
    font-size: 3.4rem;
    color: azure;
    visibility: visible;
    margin: auto;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    border-left: 2.5px solid #2AB79B;
    border-top: 2.5px solid #2AB79B;
  }
  .nav i:first-of-type {
    border-left: none;
  }
}

.sectionHome {
  background-image: url(../img/Bg_Home2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
  margin-top: -1rem;
  max-height: calc(100vh - 111.41px + 1rem);
  box-shadow: inset 0px 0px 50px 15px #015242;
}
@media (max-width: 1000px) {
  .sectionHome {
    margin-top: -1rem;
    max-height: calc(100vh - 88.13px + 1rem);
  }
}

.sectionHomeInfo {
  font-size: 5rem;
  color: azure;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: "Montserrat", sans-serif;
  padding-bottom: 5rem;
  /*

  .info{
      max-width: 50%;
  }



  */
}
@media (max-width: 1000px) {
  .sectionHomeInfo {
    flex-direction: column;
  }
}
.sectionHomeInfo .info {
  display: flex;
  margin: auto;
  margin-top: 20rem;
  align-items: baseline;
}
.sectionHomeInfo h2 {
  font-size: 6.5rem;
  margin: 1rem;
  text-shadow: 2px 2px 2px #313131;
}
@media (max-width: 1000px) {
  .sectionHomeInfo h2 {
    font-size: 2.4rem;
  }
}
.sectionHomeInfo .important {
  font-size: 6rem;
  color: #2AB79B;
  padding: 0 1rem;
  border: solid 2px #2AB79B;
  box-shadow: 0px 2px 2px #313131;
}
@media (max-width: 1000px) {
  .sectionHomeInfo .important {
    font-size: 2.4rem;
  }
}
.sectionHomeInfo .arrow {
  position: absolute;
  bottom: 10rem;
  cursor: pointer;
  animation: 1.6s UpAndDow infinite;
}
@keyframes UpAndDow {
  20%, 80% {
    transform: translateY(10px);
  }
  50%, 100% {
    transform: translateY(-10px);
  }
}

.sectionAboutMe {
  text-align: center;
  display: flex;
  flex-direction: column;
}
.sectionAboutMe h2, .sectionAboutMe h3 {
  margin: 2.5rem 0;
  color: azure;
  font-family: "Montserrat", sans-serif;
}
.sectionAboutMe .infoAboutMe {
  display: flex;
  justify-content: space-between;
}
.sectionAboutMe .infoAboutMe .profile {
  margin-left: 3rem;
}

.techAboutMe .tools {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  height: 90%;
  width: 55rem;
}
.techAboutMe .tools .icoinfo {
  width: 20rem;
  margin: auto;
}
.techAboutMe .tools .icoinfo i {
  font-size: 3rem;
  margin: 0.3rem;
  margin-top: 1rem;
}

.sectionProyect {
  text-align: center;
  display: flex;
  flex-direction: column;
}
.sectionProyect h2 {
  color: azure;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  margin: 2.5rem 0;
  margin-bottom: 5rem;
  padding: 0;
}
.sectionProyect .proyect {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
  height: 100%;
}
.sectionProyect .proyect .glass {
  width: 40rem;
  height: 25rem;
  margin: auto;
}
.sectionProyect .proyect .glass .info {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.sectionProyect .proyect .glass i {
  font-size: 2.5rem;
}
.sectionProyect .proyect .glass img {
  width: 30rem;
  height: 15.5rem;
  border: 2px solid #00735c;
  box-shadow: 0 0 3px 2px #00211A;
}

.footerAcc {
  position: relative;
  z-index: 1;
  background-color: #363636;
  height: 2rem;
  box-shadow: 0px 6px 12px #1a1a1a;
}

.footer {
  background-color: #333333;
  height: 20rem;
}
.footer .infoFooter {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
}

.webJS {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0;
  color: azure;
  font-family: "Montserrat", sans-serif;
}
.webJS i {
  font-size: 90rem;
  margin-top: -8rem;
}

