.nav{
  
  a{
    text-decoration: none;
    color: white;
    padding: 1.5rem 1rem;
    
    font-family: $titles;
    transition: .1s;
    font-weight: bold;

    &:hover{
        color: $greenPrimary;
        
    }
    &:last-of-type{
      background-color: $greenPrimary;
      padding: 1.5rem 2.5rem;
      &:hover{
        color: $greyDark;
      }
      } 
  }
  
  i{
    display: none;
    visibility: hidden;
  }
  
  @include tablet{
    background-color: $greyDark;
    box-shadow: 0px -2px 10px 0px rgb(27, 27, 27);
    position: fixed;
    width: 100%;
    z-index: 3;
    bottom: 0;
    height: 6.5rem;
    display: flex;
    justify-content: space-around;
    

    a{
      display: none;
      visibility: hidden;
      
    }
    i{
      display: flex;
      font-size: 3.4rem;
      color: azure;
      visibility: visible;
      margin: auto;
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: center;
      border-left: 2.5px solid $greenLigth;
      border-top: 2.5px solid $greenLigth;
      &:first-of-type{
        border-left: none;
      }

      
    }
    
  }
}

